import "@ui5/webcomponents/dist/Title";
import "@ui5/webcomponents/dist/Panel";
import "@ui5/webcomponents/dist/Label";
import "@ui5/webcomponents/dist/Input";
import "@ui5/webcomponents/dist/RadioButton";
import "@ui5/webcomponents/dist/RadioButtonGroup";
import "@ui5/webcomponents/dist/Link";
import "@ui5/webcomponents/dist/MessageStrip";
import "@ui5/webcomponents/dist/Select";
import "@ui5/webcomponents/dist/Option";
import "@ui5/webcomponents/dist/Button";
import "@ui5/webcomponents/dist/BusyIndicator";
import "@ui5/webcomponents-icons/dist/paste.js"
import "@ui5/webcomponents-icons/dist/message-success.js";
import "@ui5/webcomponents-icons/dist/error.js"

import { RpcBrowser } from "/node_modules/@sap-devx/webview-rpc/out.browser/rpc-browser.js";

function start() {
    //start of the program

    window.onload = (event) => {
        window.rpc.invoke('init').then((target) => {

            setEndpoint(target.defaultEndpoint);
            displayLoggedIn(target.isLoggedIn);
            displayNotLoggedIn(!target.isLoggedIn);

            target.passcodeUrl ? document.getElementById('passcodeLink').href = target.passcodeUrl : undefined;

            document.getElementById('orgSelect').addEventListener("change", (e) => {
                updateSpaces(e.detail.selectedOption.value);
                updateTargetDisplay(target);
            });

            document.getElementById('spaceSelect').addEventListener("change", (e) => {
                updateTargetDisplay(target);
            });

            document.getElementById('targetBtn').addEventListener('click', applyTarget);

        });
    };

    displayLoggedIn(false);
    displayNotLoggedIn(false);

    document.getElementById('radioSSO').addEventListener('change', (val) => {
        document.getElementById('credentials-div').style.display = "none";
        document.getElementById('sso-div').style.display = "";
    });
    document.getElementById('radioCredentials').addEventListener('change', (val) => {
        document.getElementById('credentials-div').style.display = "";
        document.getElementById('sso-div').style.display = "none";
    });

    document.getElementById('loginBtn').addEventListener('click', () => {

        let payload = {};
        payload.endpoint = document.getElementById('endpointInput').value;
        if (document.getElementById('radioSSO').checked) {
            payload.ssoPasscode = document.getElementById('passcodeInput').value;
        } else {
            payload.user = document.getElementById('usernameInput').value;
            payload.password = document.getElementById('passwordInput').value;
        }

        window.rpc.invoke('loginClick', [payload]).then((res) => {
            if (res) {
                displayLoggedIn(true);
                displayNotLoggedIn(false);
            } else {
                // TODO: login failed
            }
        })
    });

    document.getElementById('logoutBtn').addEventListener('click', () => {

        let payload = {};
        window.rpc.invoke('logoutClick', [payload]).then((res) => {
            if (res) {
                displayLoggedIn(false);
                displayNotLoggedIn(true);
            } else {
                // TODO show error in logout
            }
        })
    });


    document.getElementById('pasteIcon').addEventListener('click', () => {
        navigator.clipboard.readText().then(
            clipText => document.getElementById('passcodeInput').value = clipText);

    });

}

function setEndpoint(endpoint) {
    if (endpoint) {
        document.getElementById('endpointInput').value = endpoint;
        document.getElementById('endpointLabel').innerHTML = endpoint;
    }
}
function displayLoggedIn(display) {
    const loggedInCollection = document.getElementsByClassName("loggedIn");

    for (let i = 0; i < loggedInCollection.length; i++) {
        display ? loggedInCollection.item(i).style.display = "" : loggedInCollection.item(i).style.display = "none";
    }

    // set the target values
    setTarget();
}

function displayNotLoggedIn(display) {
    const notLoggedInCollection = document.getElementsByClassName("notLoggedIn");

    for (let i = 0; i < notLoggedInCollection.length; i++) {
        display ? notLoggedInCollection.item(i).style.display = "" : notLoggedInCollection.item(i).style.display = "none";
    }
}

function setTarget() {
    window.rpc.invoke('getSelectedTarget').then((target) => {

        updateTargetDisplay(target);

        window.rpc.invoke('getOrgs').then((orgs) => {
            const orgElement = document.getElementById("orgSelect");
            // const current = orgElement.selected;
            // orgElement.selected = undefined;
            while (orgElement.firstChild) {
                orgElement.removeChild(orgElement.firstChild);
            }

            let orgSelected = false;
            orgs.forEach(org => {
                // using only labels, as "cf org" command doesn't return guid
                if (org.label == target.org) {
                    orgSelected = true;
                    updateSpaces(org.guid, target.space);
                }

                orgElement.appendChild(new Option(org.label, org.guid, false, org.label == target.org));
            });
            if (!orgSelected && orgs && orgs.length > 0) {
                updateSpaces(orgs[0].guid, target.space);
            }
        });
    });
}

function updateTargetDisplay(target) {
    const targetMessage = document.getElementById("targetMessage");
    const applyButton = document.getElementById("targetBtn");

    if (target && target.org && target.space) {
        targetMessage.innerText = `Organization and space are set to ${target.org} / ${target.space}`;
        targetMessage.style.visibility = "";
    } else {
        targetMessage.style.visibility = "none";
    }

    const org = getSelected(document.getElementById("orgSelect"))?.label;
    const space = getSelected(document.getElementById("spaceSelect"))?.label;
    if (org === target.org && space === target.org) {
        applyButton.disabled = true;
    } else {
        applyButton.disabled = false;
    }

}

function updateSpaces(org, selectedSpace) {
    window.rpc.invoke('getSpaces', [org]).then((spaces) => {
        const spaceElement = document.getElementById("spaceSelect");
        while (spaceElement.firstChild) {
            spaceElement.removeChild(spaceElement.firstChild);
        }
        spaces.forEach(space => {
            const selected = selectedSpace !== undefined && space.label === selectedSpace;
            spaceElement.appendChild(new Option(space.label, space.guid, false, selected));
        });
    });
}

function applyTarget() {
    const org = getSelected(document.getElementById("orgSelect")).label;
    const space = getSelected(document.getElementById("spaceSelect")).label;

    window.rpc.invoke('applyTarget', [org, space]).then(() => {
        updateTargetDisplay({ org: org, space: space });
    });
}

function getSelected(e) {
    let ret;
    e.options.forEach(element => {
        if (element.selected) {
            ret = element;
            return;
        }
    });
    return ret;
}

function showProgress(active) {
    document.getElementById("busyIndicator").active = active;
    return "return nothing";
}

const vscode = acquireVsCodeApi();

// logger is optional third parameter, implementing interface IChildLogger:
// https://github.com/SAP/vscode-logging/blob/master/packages/types/api.d.ts#L17
window.rpc = new RpcBrowser(window, vscode);
window.rpc.registerMethod({ func: showProgress, thisArg: this, name: "showProgress" });

start();
